.order_wrapper {
  border: 2px solid var(--black);
  border-radius: 21px;
  height: 100%;
  padding: 20px;
  max-width: 500px;
  width: 100%;
}
.order_details_title {
  font-weight: 800;
  font-size: 28px;
}
.order_total_sum {
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin: 55px 0 55px;
}
.total_sum {
  font-weight: 500;
  font-size: 24px;
}

.amount {
  font-weight: 600;
  font-size: 32px;
}

.amount span {
  font-size: 10px;
}
