.ui_btn {
  border-radius: 10px;
  border: none;
  cursor: pointer;
  font-weight: 700;
  transition: background-color 0.3s;
  font-family: Montserrat;
  font-size: 30px;
  background-color: var(--ui-color);
  color: var(--white);
}
.ui_btn.header_btn {
  width: 125px;
  height: 50px;
  font-size: 16px;
}
.ui_btn.sale_btn {
  background-color: var(--white);
  color: var(--black);
  font-size: 25px;
  width: 155px;
  height: 80px;
  font-family: Montserrat;
}
.ui_btn.get_discount {
  background-color: transparent;
  border: 2px solid var(--white);
  border-radius: 20px;
  max-width: 473px;
  width: 100%;
  height: 75px;
  margin-top: 5px;
}
.ui_btn.to_cart_btn {
  width: 340px;
  height: 85px;
  margin-top: 40px;
  margin-bottom: 40px;
}
.ui_btn.back_btn {
  background-color: transparent;
  color: var(--black);
  font-weight: 500;
  font-size: 20px;
}
.ui_btn.order_btn {
  max-width: 445px;
  width: 100%;
  height: 75px;
  margin-bottom: 25px;
  border-radius: 20px;
}
.ui_btn.add_to_cart_btn {
  position: absolute;
  z-index: 90;
  max-width: 280px;
  width: 100%;
  height: 72px;
  border: 2px solid var(--ui-color);
  background-color: var(--f1-white);
  color: var(--ui-color);
  font-weight: 600;
  font-size: 18px;
  border-radius: 20px;
  padding: 20px 60px;
  top: 55%;
  left: 50%;
  transform: translateX(-50%);
}

.ui_btn:hover {
  background-color: var(--blue);
}
.ui_btn.add_to_cart_btn:hover {
  background-color: var(--ui-color);
  color: var(--white);
}
.ui_btn.remove_btn:hover {
  color: var(--red);
  background-color: transparent;
}

.ui_btn:active {
  transform: translateY(2px);
}
.ui_btn.add_to_cart_btn:active {
  transform: translate(-50%, 2px);
}

@media (max-width: 768px) {
  .ui_btn.to_cart_btn {
    margin-top: 20px;
    margin-bottom: 20px;
  }
}
